<template>
  <button @click="click">
    <svg
      :class="{click: isFavorite}"
      width="24"
      height="24"
      viewBox="0 0 24 24"
      fill="none"
      xmlns="http://www.w3.org/2000/svg">
      <path
        d="M11.9996 21.0542C-8 10 5.99999 -1.99997 11.9996 5.58809C18 -1.99997 32 10 11.9996 21.0542Z"
        stroke="#000"
        stroke-width="1.8" />
    </svg>
  </button>
</template>

<script>
export default {
  name: 'HeartBtn',
  props: {
    isFavorite: Boolean,
  },
  methods: {
    click(a) {
      if (a.target.localName === 'svg') {
        a.target.classList.toggle('click');
      } else {
        a.target.parentNode.classList.toggle('click');
      }
    },
  },
};
</script>

<style scoped>
button {
  background-color: white;
  border: none;
  margin: 0;
  padding: 0;
}
button:hover svg path,
button:hover svg circle {
  stroke: gray;
}
button.click svg {
  fill: red;
}
.click {
  fill: red;
}
</style>
